.infoCard {
    background-color: #000000ce;
    padding: 10px;
    border-radius: 10px;
    margin: 10px;
}

.infoCard h2 {
    letter-spacing: 0.1ch;
    color: rgb(118, 3, 194);
    margin-bottom: 5px;
}

.infoCard p {
    color: #fff;
    font-weight: 500;
}

.infoCard button {
    outline: none;
    border: none;
    border-radius: 0.2rem;
    padding: 0.3rem  1rem;
    cursor: pointer;
    color: #fff;
    background-color: rgb(134, 3, 194);
}

.infoCard button:hover {
    background-color: rgba(181, 2, 197, 0.8);
}