.search {
  background-color: #000000;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
}

.searchNavContainer {
  width: min-content;
  height: 100%;
  box-sizing: border-box;
  padding: 1rem 2rem;
}

.searchMain {
  width: 100%;
  height: 100%;
  padding: 2rem 3rem;
  box-sizing: border-box;
  background: linear-gradient(rgba(8, 8, 8, 0.76), rgba(0, 0, 0, 0.568)), url(https://mir-s3-cdn-cf.behance.net/project_modules/fs/5ac05074318367.5c2c28978c4c8.jpg) no-repeat 50% fixed;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
}

.searchSeriesGrid {
    width: 100%;
    padding: 2rem 5rem 2rem 2.5rem;
    box-sizing: border-box;
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    align-items: center;
    justify-items: center;
  }


  @media only screen and (max-width: 600px) {
    .search {
      flex-direction: column-reverse;
      box-sizing: border-box;
      align-items: flex-start;
      justify-content: center;
    }
  
    .searchNavContainer {
      height: min-content;
      width: 100%;
      box-sizing: border-box;
      padding: 0rem;
    }
    
    .searchMain {
      width: 100%;
      height: 100%;
      padding: 1rem;
      padding-bottom: 5rem;
    }
  
    .searchSeriesGrid {
      padding: 1rem;
      grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
    }
  }

  
